import { Component, OnInit } from '@angular/core';
import { AbsensiKehadiranService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router } from '@angular/router';
import { NzMessageService } from 'ng-zorro-antd';
import { Cuti_pengajuan } from 'src/app/sdk/hr/models';
import { PegawaiDetailService } from '../../service/pegawai-detail.service';

@Component({
    selector: 'app-list',
    templateUrl: './list.component.html',
    styleUrls: ['./list.component.scss']
})
export class ListComponent implements OnInit {

    constructor(
        private absensiKehadiranService: AbsensiKehadiranService,
        private spinner: NgxSpinnerService,
        private router: Router,
        private nzMessageService: NzMessageService,
        private pegawaiDetailService: PegawaiDetailService
    ) { }

    id_pegawai: string = ''
    dataPengajuanCuti: Cuti_pengajuan[] = []
    id_cuti_pengajuan: string = ''
    ngOnInit() {


        this.pegawaiDetailService.getIdPegawai().subscribe(data => {
            this.id_pegawai = data

            this.getData()
        })

    }
    date = new Date()
    lisData = []
    getData() {

        let param = {
            id_pegawai: this.id_pegawai,
            bulan: this.date.getMonth() + 1,
            tahun: this.date.getFullYear()
        }
        this.absensiKehadiranService.getAbsensiKehadirans(JSON.stringify(param)).subscribe(
            data => {
                this.lisData = data.data
            }
        )
    }


}
