import { Component, OnInit } from '@angular/core';
import { AbsensiKehadiranService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { NzDrawerService } from 'ng-zorro-antd';
import { DetailComponent } from '../detail/detail.component';

@Component({
    selector: 'app-aktifitas',
    templateUrl: './aktifitas.component.html',
    styleUrls: ['./aktifitas.component.scss']
})
export class AktifitasComponent implements OnInit {

    constructor(
        private absensiKehadiranService: AbsensiKehadiranService,
        private spinner: NgxSpinnerService,
        private drawerService: NzDrawerService
    ) { }

    ngOnInit() {
        this.getData()
    }

    listdata = [];

    jumlah_hari = 0
    tanggal: Date = new Date()
    getData() {
        let param = {
            bulan: this.tanggal.getMonth() + 1,
            tahun: this.tanggal.getFullYear(),
        }

        this.spinner.show()
        this.absensiKehadiranService.getAbsensiKehadirans(JSON.stringify(param)).subscribe(
            data => {
                this.spinner.hide()
                this.listdata = data.data['data']
                this.jumlah_hari = data.data['out_hari']
            }
        )
    }

    loadingTable = false
    changePage() {
        this.loadingTable = true;
        console.log(this.loadingTable)
        setTimeout(() => {
            this.loadingTable = false
        }, 10000);
    }


    // detail
    detail(id_rekap_kehadiran): void {


        const drawerRef = this.drawerService.create<DetailComponent, { id_rekap_kehadiran: string }, string>({
            nzTitle: 'Detail Absensi',
            nzContent: DetailComponent,
            nzWidth: '500px',
            nzContentParams: {
                id_rekap_kehadiran: id_rekap_kehadiran
            }
        });

        drawerRef.afterOpen.subscribe(() => {
            console.log('Drawer(Component) open');
        });

        drawerRef.afterClose.subscribe(data => {
            console.log(data);
            if (typeof data === 'string') {

            }
        });
    }



}
