import { Component, OnInit, Input } from '@angular/core';
import { NzDrawerRef } from 'ng-zorro-antd';
import { AbsensiKehadiranService, KalenderCutiPengajuanTanggalService, KalenderLemburService, DirektoriPerjalananDinasRonService } from 'src/app/sdk/hr/services';
import { Kehadiran } from 'src/app/sdk/hr/models';
import { AbsensiRekapKehadiranService } from 'src/app/sdk/hr/services/absensi-rekap-kehadiran.service';
import { Rekap_kehadiran } from 'src/app/sdk/hr/models/rekap-_kehadiran';

@Component({
    selector: 'app-detail',
    templateUrl: './detail.component.html',
    styleUrls: ['./detail.component.scss']
})
export class DetailComponent implements OnInit {

    @Input() id_rekap_kehadiran = '';

    constructor(
        private drawerRef: NzDrawerRef<string>,
        private absensiRekapKehadiranService: AbsensiRekapKehadiranService,
        private kalenderCutiPengajuanTanggalService: KalenderCutiPengajuanTanggalService,
        private kalenderLemburService: KalenderLemburService,
        private direktoriPerjalananDinasRonService: DirektoriPerjalananDinasRonService,
    ) { }
    ngOnInit(): void {
        this.getData()
    }

    close(): void {
        this.drawerRef.close(null);
    }

    kehadiran: Rekap_kehadiran = {}
    tanggal = new Date()
    id_pegawai = ''
    getData() {
        this.absensiRekapKehadiranService.getAbsensiRekapKehadiransId(this.id_rekap_kehadiran).subscribe(
            data => {
                this.kehadiran = data.data
                this.id_pegawai = data.data.id_pegawai
                this.tanggal = new Date(data.data.jadwal_jam_masuk)
                this.getLembur()
                this.getOnduty()
                this.getCuti()
            }
        )
    }

    lembur = []
    getLembur() {
        this.kalenderLemburService.getKalenderLemburs(JSON.stringify({ tanggal: this.tanggal, id_pegawai: this.id_pegawai })).subscribe(
            data => {
                this.lembur = data.data
            }
        )
    }

    cuti = []
    getCuti() {
        this.kalenderCutiPengajuanTanggalService.getKalenderCutiPengajuanTanggals(JSON.stringify({ tanggal: this.tanggal, id_pegawai: this.id_pegawai })).subscribe(
            data => {
                this.cuti = data.data
            }
        )
    }

    onduty = []
    getOnduty() {
        this.direktoriPerjalananDinasRonService.getDirektoriPerjalananDinasRons(JSON.stringify({ tanggal: this.tanggal, id_pegawai: this.id_pegawai })).subscribe(
            data => {
                this.onduty = data.data
            }
        )
    }


}
